import {
  LOGIN_USER,
  LOGIN_USER_SUCCESS,
  LOGIN_USER_ERROR,
  REGISTER_USER,
  REGISTER_USER_SUCCESS,
  REGISTER_USER_ERROR,
  LOGOUT_USER,
  FORGOT_PASSWORD,
  FORGOT_PASSWORD_SUCCESS,
  FORGOT_PASSWORD_ERROR,
  RESET_PASSWORD,
  RESET_PASSWORD_SUCCESS,
  RESET_PASSWORD_ERROR,
  VERIFY_OTP,
  VERIFY_OTP_ERROR,
  PROFILE_ADD_DATA,
  PROFILE_ADD_DATA_SUCCESS,
  PROFILE_ADD_DATA_ERROR,
} from '../actions';
import { getCurrentUser, getProfileData } from '../../helpers/Utils';
// import { isAuthGuardActive, currentUser } from "../../constants/defaultValues";

const INIT_STATE = {
  currentUser: getCurrentUser(),
  profileData: getProfileData(),
  loading: false,
  error: '',
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case LOGIN_USER:
      return { ...state, loading: true, error: '' };
    case LOGIN_USER_SUCCESS:
      return {
        ...state,
        loading: false,
        currentUser: action.payload,
        error: '',
      };
    case LOGIN_USER_ERROR:
      return {
        ...state,
        loading: false,
        currentUser: null,
        error: action.payload.message,
      };

    case FORGOT_PASSWORD:
      return { ...state, loading: true, error: '' };
    case FORGOT_PASSWORD_SUCCESS:
      return {
        ...state,
        loading: false,
        forgotUserMail: action.payload,
        error: '',
      };
    case FORGOT_PASSWORD_ERROR:
      return {
        ...state,
        loading: false,
        forgotUserMail: '',
        error: action.payload.message,
      };

    case RESET_PASSWORD:
      return { ...state, loading: true, error: '' };
    case RESET_PASSWORD_SUCCESS:
      return {
        ...state,
        loading: false,
        newPassword: action.payload,
        resetPasswordCode: '',
        error: '',
      };
    case RESET_PASSWORD_ERROR:
      return {
        ...state,
        loading: false,
        newPassword: '',
        resetPasswordCode: '',
        error: action.payload.message,
      };

    case REGISTER_USER:
      return { ...state, loading: true, error: '' };
    case REGISTER_USER_SUCCESS:
      return {
        ...state,
        loading: false,
        error: '',
      };
    case REGISTER_USER_ERROR:
      return {
        ...state,
        loading: false,
        currentUser: null,
        error: action.payload.message,
      };

    case VERIFY_OTP:
      return {
        ...state,
        loading: true,
        error: '',
      };
    case VERIFY_OTP_ERROR:
      return {
        ...state,
        loading: false,
        currentUser: null,
        error: action.payload.message,
      };

    case PROFILE_ADD_DATA:
      return { ...state, loading: true, error: '' };
    case PROFILE_ADD_DATA_SUCCESS: {
      return {
        ...state,
        profileData: action.payload,
        loading: false,
      };
    }
    case PROFILE_ADD_DATA_ERROR: {
      return {
        ...state,
        loading: false,
        error: action.payload.message,
      };
    }

    case LOGOUT_USER:
      return { ...state, currentUser: null, error: '' };

    default:
      return { ...state };
  }
};
